import { useState } from 'react';

const Ostos = ({ tuote }) => {
    const [showOstot, setShowOstot] = useState(true);

    return (
        <div className="tuotteet">
            <p onClick={() => setShowOstot(!showOstot)}></p>
            {showOstot &&
                <div>
                    <p>{tuote.nimi}: {tuote.maara}kpl</p>
                    {/* <p>{tuote.hinta * tuote.maara}€</p> */}
                </div>}
        </div>
    )
}

const Ostoskori = ({ tuotteet, setTuotteet }) => {
    const calcTotal = () => {
        const maksu = tuotteet.map(l => l.hinta * l.maara); //[1, 2, 3, 4]
        return maksu.reduce((a, b) => Math.round((a + b + Number.EPSILON) * 100) / 100)
    }

    const setToZeros = () => {
        const tempTuoteet = tuotteet.map(tuot => {
            tuot = { ...tuot, maara: 0 };
            return tuot;
        });
        setTuotteet(tempTuoteet);
    }

    const filteredProducts = tuotteet.filter(c => c.maara > 0);
    return (
        <div class="kori">
            <button onClick={() => setToZeros()}>Tyhjennä</button>
            {filteredProducts.map(c => <Ostos key={c.id} tuote={c} />)}<br />
            <p className="hinta">Hinta: {calcTotal()}€</p>
        </div>
    )
}


export { Ostoskori };