import '../App.css';
import React, { useState } from 'react';

const Tuote = ({ tuote, lisaaOstos, poistaOstos, setToZeros }) => {

    const [showTuote, setShowTuote] = useState(true);
    return (
        <div class="row">
            <p onClick={() => setShowTuote(!showTuote)}></p>
            {showTuote &&
            <div className="tuotteet">
                <div className="tuotteet2">
                    <p>{tuote.nimi}</p>
                    <p>{tuote.hinta}€</p>
                    <img alt="kuva" className="tuote" src={tuote.kuva} /><br/>
                    <div class="center">
                        <button onClick={() => poistaOstos(tuote.id)}>-</button>
                        {tuote.maara}
                        <button onClick={() => lisaaOstos(tuote.id)}>+</button>
                    </div>
                </div>
            </div>}
            <br />
        </div>
    )
}

const Tuotteet = ({ tuotteet, setTuotteet }) => {
    const lisaaOstos = id => {
        const tempTuoteet = tuotteet.map(l => {
            if (id === l.id) {
                return { ...l, maara: l.maara + 1 };
            } else
                return l;
        })
        setTuotteet(tempTuoteet);
    }
    const poistaOstos = id => {
        const tempTuoteet = tuotteet.map(l => {
            if (id === l.id && l.maara > 0) {
                return { ...l, maara: l.maara - 1 };
            } else
                return l;
        })
        setTuotteet(tempTuoteet);
    }

    return (
        <div>
            <div class="container">
                {tuotteet.map(c => <Tuote key={c.id} tuote={c} lisaaOstos={lisaaOstos} poistaOstos={poistaOstos} />)}
            </div >
            <hr />
            <h2>Ostoskori</h2><br />
        </div>
    )
}
export default Tuotteet;