import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import './App.css';
import Tuotteet from './components/tuotteetInfo';
import tuotteetData from './components/tuotteetData';
import { Ostoskori } from './components/ostoskori';
import { Vahvistus } from './components/varmista';
function App() {

  const [tuotteet, setTuotteet] = useState(tuotteetData);
  const [page, setPage] = useState(0);
  const name = "Arlet";
  
  return (
      <div>
    <header className="App-header">
        <h1 class name ="App">
          Tämä on {name}in fanikauppa
        </h1>
      </header>
        <div className="phoneBook">
          {page === 0 &&
            <div>
            <h2>Tuotteet</h2>
              <Tuotteet tuotteet={tuotteet} setTuotteet={setTuotteet} />
              <button onClick={() => setPage(page + 1)}>Vahvista</button>
              <Ostoskori tuotteet={tuotteet} setTuotteet={setTuotteet} />
            </div>

          }{page === 1 &&
            <div>
              <Vahvistus tuotteet={tuotteet} setTuotteet={setTuotteet} />
              <button onClick={() => setPage(page - 1)}>Etusivu</button>
            </div>
          }
        </div>
    </div>
  );
}

export default App;
